
<template>
<!--------->
  <div id="login-warpper">
    <div>
        <button><router-link to="/Reg" style="color:#eee;"></router-link></button>
    </div>
    <div id="logo"></div>
    <div id="login-d">
      <h2 style="margin:5px 10px;"> :</h2>
      <div class="lg">
        :
        <div class="lginpt">
          <div class="ipt">
            <input type="text" id="username" placeholder="" v-model="username">
          </div>
        </div>
      </div>
      <div class="lg">
        :
        <div class="lginpt">
          <div class="ipt">
            <input type="password" id="pass" placeholder="" v-model="pass">
          </div>
        </div>
      </div>
      <div style="clear:both"></div>
      <div class="lginpt" style="background-color:#fff;">
        <button
          v-on:click="login"
          type="submit"
          style="float:right;"
        ></button>
      </div>
      <p class="error">{{error}}</p>
    </div>
  </div>
</template>
<script>
import auth from "../../auth";
export default {
  name: "login",
  data() {
    return {
      username: '',
      pass: '',
      code: '',
      error:''
    }
  },
  methods: { //Auth.js 
    login() {
        localStorage.CIC=0
        auth.login(this.$data.username, this.$data.pass,loggedIn => {
        if (!loggedIn) {
          this.error = ""
        } else {
          this.error = ""
          localStorage.username=this.$data.username
          this.$router.replace(this.$route.query.redirect || '/')
        }
      })
    }
  }
};
</script>
<style src="../../css/login.css" scoped>

</style>

<style scoped>
.error{
    color: red;
}
</style>
