<template>
<!---
 divtab
 ---->
  <div id="login-warpper">
    <div>
      <button>
        <router-link to="/Login" style="color:#eee;"></router-link>
      </button>
    </div>
    <div id="login-d">
      <h2 style="margin:5px 10px;">:</h2>
      <div class="lg">
        :
        <div class="lginpt">
          <div class="ipt">
            <input type="text" id="username" placeholder="" v-model="username">
          </div>
        </div>
      </div>

      <div class="lg">
        :
        <div class="lginpt">
          <div class="ipt">
            <input type="text" id="phone" placeholder="" v-model="phone">
          </div>
        </div>
      </div>
      <div class="lg">
        :
        <div class="lginpt">
          <div class="ipt">
            <input type="password" id="password" placeholder="" v-model="password">
          </div>
        </div>
      </div>
      <div class="lg">
        :
        <div class="lginpt">
          <div class="ipt">
            <input type="mail" id="mail" placeholder="" v-model="mail">
          </div>
        </div>
      </div>
      <div class="lg">
        :
        <div class="lginpt">
          <div class="ipt">
            <input
              type="password"
              id="password2"
              placeholder=""
              v-model="password2"
            >
          </div>
        </div>
      </div>
      <div class="lg">
        :
        <div class="lginpt" style="background-color: #fff;">
          <div class="ipt">
            <div style="float:left;width:100px;">
              <div class="fl">
                <input type="radio" id="male" name="sex" v-on:change="male">
                <label for="male"></label>
              </div>
              <div style="float:left"></div>
            </div>
            <div style="float:left;width:100px;">
              <div class="fl">
                <input type="radio" id="female" name="sex" v-on:change="female">
                <label for="female"></label>
              </div>
              <div class="fl"></div>
            </div>
          </div>
        </div>
      </div>
      <div class="lginpt" style="background-color:#fff;width: 700px;margin: 0px;">
        <button
          v-on:click="dealdata"
          type="submit"
          style="background-color:blueviolet;float: right;"
        ></button>
        <div style="color:red;float: right;">{{perror}}</div>
      </div>
      <div style="clear:both"></div>
    </div>
  </div>
</template>
<script>
import axios from "../../http/axios";
export default {
  data() {
    return {
      UID:"",
      username: "",
      password: "",
      password2: "",
      code: "",
      phone: "",
      mail: "",
      sex: "",
      error: 1
    };
  },
  computed: {
    perror: function() {
      if (this.$data.password != this.$data.password2) {
        return "";
      }
      return "";
    }
  },
  methods: {
    male: function() {
      this.$data.sex = "m";
    },
    female: function() {
      this.$data.sex = "f";
    },
    dealdata: function() { //
      try {
        if (this.$data.username != "") {
          if (this.$data.password == this.$data.password2) {
            var obj = new Object();
            obj.username = this.$data.username;
            obj.passwd = this.$data.password;
            obj.mail = this.$data.mail;
            obj.phone = this.$data.phone;
            obj.sex = this.$data.sex;
            var uinfo = JSON.stringify(obj);
            axios
              .post("/serverA/controller.php", uinfo, {
                headers: {
                  'APPFUNC': 1
                }
              })
              .then(res => {
                //console.log(res.data)
                switch (res.data.status) {
                  case '0':
                    alert("");
                    break;
                  case '3':
                    alert("");
                    break;
                  case '1':
                    var obj2 = new Object();
                    obj2.username = this.$data.username;
                    obj2.passwd = this.$data.password;
                    obj2.mail = this.$data.mail;
                    obj2.phone = this.$data.phone;
                    obj2.UID=res.data.UID;
                    this.$data.error = 0;
                    var uinfo2 = JSON.stringify(obj2);
                    axios
                      .post("/serverB/controller.php", uinfo2, {
                        headers: {
                          'APPFUNC': 1
                        }
                      })
                      .then(function(res2) {
                        //console.log(res2.data)
                        switch (res2.data) {
                          case 1:
                            alert("");
                            break;
                        }
                      });
                    break;
                }
              });
          } else {
            alert("!");
          }
        } else {
          alert("");
        }
      } catch (error) {
        alert("");
      }
    }
  }
};
</script>
<style src="../../css/login.css" scoped>
</style>
<style scoped>
#login-d {
  width: 800px;
  height: 440px;
  border-radius: 1%;
}

#login-d div {
  float: left;
}
</style>
