import Vue from 'vue'
import VueRouter from 'vue-router'
import auth from './auth'
import RouterPage from './RouterPage.vue'
import Login from './components/UserAuth/Login.vue'
import Registme from './components/UserAuth/Reg.vue'
import Cloud from './components/CloudManager/Cloud.vue'

Vue.use(VueRouter)

function requireAuth (to, from, next) {
  if (!auth.loggedIn()) {
    next({
      path: '/login',
      query: { redirect: to.fullPath }
    })
  } else {
    next()
  }
}

const router = new VueRouter({ // 
  mode: 'history',
  base: './',
  routes: [
    { path: '/Cloud', component: Cloud, beforeEnter: requireAuth }, //
    { path: '/login', component: Login },
    { path: '/reg', component: Registme },
    { path: '/', redirect: './Cloud' },
    { path: '/logout',
      beforeEnter (to, from, next) {
        auth.logout()
        next('/login') // logouturl
      }
    }
  ]
})

new Vue({
  router,
  render: h => h(RouterPage) //RouterPage
}).$mount('#app')
